// SideAdContainer.jsx
import React from 'react';
import styled from 'styled-components';

const SideAdContainer = ({ children, side }) => {
  return (
    <AdContainer side={side}>
      {/*    */}
      <AdImage src={process.env.PUBLIC_URL + '/image/ad.png'} alt="Ad Image" />
      {children}
    </AdContainer>
  );
};

const AdContainer = styled.div`
  position: fixed;
  ${({ side }) => (side === 'left' ? 'left: 0;' : 'right: 0;')}
  top: 50%;
  transform: translateY(-45%);
  background-color: #f0f0f0;
  padding: 10px;
  z-index: 100; /* Adjust the z-index value as needed */
`;

const AdImage = styled.img`
  width: 194px;
  height: 817px;
`;

export default SideAdContainer;
