import React from 'react';
import styled from 'styled-components';

function AddProductButton({ display, onAddProductListClick }) {
  return (
    <AddProductButtonWrapper isDisplay={display}>
      <AddProductBtn onClick={onAddProductListClick}>
        <AddProductButtonContent></AddProductButtonContent>
      </AddProductBtn>
    </AddProductButtonWrapper>
  );
}

const AddProductButtonWrapper = styled.div`
  display: ${props => (props.isDisplay ? 'block' : 'none')};
  width: 990px;
  margin-top: 10px;
  margin-left: -10px;
  margin-bottom: 10px;
`;

const AddProductBtn = styled.button`
  display: block;
  width: 100%;
  height: 43px;
  border: 1px solid #bfbfbf;
  border-bottom: 1px solid #aeaeae;
  font-weight: bold;
  font-size: 15px;
  color: #666;
  background-color: transparent;
`;

const AddProductButtonContent = styled.span`
  margin-right: 5px;
  color: #555;
  &: after {
    display: inline-block;
    position: relative;
    left: 8px;
    top: -3px;
    width: 6px;
    height: 6px;
    content: '';
    transform: rotate(45deg);
    border-right: 2px solid;
    border-bottom: 2px solid;
    border-color: #777;
  }
`;

export default AddProductButton;
