import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import { Link } from 'react-router-dom';

const DIVIDE_CATEGORIES = 6;

function SiteMap({ isDisplayed }) {
  return (
    <TopWrapper isDisplayed={isDisplayed}>
      <Wrapper>
        <MenuTitle>SITEMAP</MenuTitle>
        <SiteMapUpperWrapper>
          {categories.slice(0, DIVIDE_CATEGORIES).map(category => (
            <SubCategories key={category.id}>
              <SubCategoriesTitle>{category.categoryName}</SubCategoriesTitle>

              {category.subcategories.map((subcategory, index) => (
                <SubCategory key={index}>
                  <SubCategoryLink to="">
                    {subcategory.categoryName}
                  </SubCategoryLink>
                </SubCategory>
              ))}
            </SubCategories>
          ))}
        </SiteMapUpperWrapper>

        <SiteMapLowerWrapper>
          {categories
            .slice(DIVIDE_CATEGORIES, categories.length)
            .map((category, index) => (
              <SubCategories key={index}>
                {category.categoryName ? (
                  <SubCategoriesTitle>
                    {category.categoryName}
                  </SubCategoriesTitle>
                ) : null}

                {category.subcategories
                  ? category.subcategories.map((subcategory, index) => (
                      <SubCategory key={index}>
                        <SubCategoryLink to="">
                          {subcategory.categoryName}
                        </SubCategoryLink>
                      </SubCategory>
                    ))
                  : ''}
              </SubCategories>
            ))}
        </SiteMapLowerWrapper>
      </Wrapper>
    </TopWrapper>
  );
}

const TopWrapper = styled.div`
  position: absolute;
  top: 80px;
  width: 100%;
  display: ${props => (props.isDisplayed ? 'flex' : 'none')};
  justify-content: center;
  background-color: #333;
  padding-top: 50px;
  padding-bottom: 50px;
  z-index: 1000;
`;

const Wrapper = styled.div`
  width: 1100px;
  display: flex;
  flex-direction: column;
  align-items: start;
`;

const MenuTitle = styled.p`
  font-size: 1.5em;
  color: #fff;
  line-height: 1.1;
  padding: 0;
  margin: 0;
`;

const SiteMapUpperWrapper = styled.div`
  width: inherit;
  display: flex;
  justify-content: space-between;
  margin-bottom: 50px;
`;

const SiteMapLowerWrapper = styled.div`
  width: inherit;
  display: flex;
  justify-content: space-between;
  margin-top: -230px;
`;

const SubCategories = styled.ul`
  width: 170px;
  padding: 0;
  text-align: center;
`;

const SubCategory = styled.li`
  padding-bottom: 10px;
`;

const SubCategoryLink = styled(Link)`
  color: #bbb;
`;

const SubCategoriesTitle = styled.p`
  text-align: center;
  border-width: 1px 0;
  border-color: rgba(68, 68, 68, 0.5);
  border-style: solid;
  padding: 12px 0;
  font-size: 1.2em;
  color: #fff;
  margin: 20px 0;
`;

export default SiteMap;

