import React, { createContext, useState, useContext } from 'react';
import { BookingContext } from './../../Booking';
import TimePartList from './TimePartList';
import TimeList from './TimeList';
import { createDayDateTimes, setDateTime, today } from './utils';
import styled from 'styled-components';

const time = createDayDateTimes(setDateTime(today, 0), setDateTime(today, 9));
export const TimeContext = createContext({
  timeList: [],
});

function Time() {
  const {
    movieValue,
    movieStartTime,
    locationValue,
    bookingActive,
    movieEndTime,
  } = useContext(BookingContext);
  const [timeList, setTimeList] = useState(time);
  const [timeActive, setTimeActive] = useState(false);
  const contextValue = {
    timeList,
    setTimeList,
  };

  const handleTimeClick = () => {
    setTimeActive(true);
    alert(`
       4      18 /   .
    - 18    4  (1 2 )
    - 18    2 
    -       
    (,    )
    
             .        .
    
    [ ]
             .
          .
    
            .`);
  };
  return (
    <TimeContext.Provider value={contextValue}>
      <TimeWrapper>
        <TimeTitleWrapper>
          <TimeTitle></TimeTitle>
          <TimePartList />
        </TimeTitleWrapper>
        <TimeList />
        {bookingActive && (
          <ShowMovieDataList>
            {movieStartTime.map((item, key) => {
              return (
                <ShowMovieBtn role="button" key={key} onClick={handleTimeClick}>
                  <ShowTimeWrapper>
                    <StartTime>{item}</StartTime>
                    <EndTime>~{movieStartTime[key]}</EndTime>
                  </ShowTimeWrapper>
                  <ShowTitle>{movieValue}</ShowTitle>
                  <ShowLoaction>{locationValue}</ShowLoaction>
                </ShowMovieBtn>
              );
            })}
          </ShowMovieDataList>
        )}
      </TimeWrapper>
    </TimeContext.Provider>
  );
}

const TimeWrapper = styled.div`
  width: 435px;
  height: 530px;
  border-right: 1px solid #d8d9db;
  border-bottom: 1px solid #d8d9db;
`;

const TimeTitleWrapper = styled.div`
  display: flex;
  width: 100%;
  align-items: center;
  border-bottom: 1px solid #d8d9db;
`;

const TimeTitle = styled.h3`
  margin: 10px;
  font-size: 16px;
  font-weight: 600;
  color: #444;
`;

const ShowMovieDataList = styled.ul`
  width: 100%;
  height: 100%;
`;

const ShowMovieBtn = styled.li`
  display: flex;
  padding: 10px 3px 10px 20px;
  border-bottom: 1px solid #d8d9db;
  font-family: -apple-system, sans-serif;
  &:hover {
    color: #fff;
    background: #666;
  }
`;

const ShowTimeWrapper = styled.div``;

const StartTime = styled.div`
  font-weight: 700;
  font-size: 20px;
  font-family: system-ui;
`;
const EndTime = styled.div`
  font-size: 16px;
  font-family: system-ui;
  color: #bcbcbc;
`;

const ShowTitle = styled.div`
  margin-left: 10px;
  font-size: 16px;
`;

const ShowLoaction = styled.div`
  margin-left: auto;
  font-size: 13px;
`;
export default Time;
