import React, { useState } from 'react';
import { withRouter, Link } from 'react-router-dom';
import { API } from '../../config';
import { customFetch } from '../../utils/api';
import Modal from '../../components/Modal/Modal';
import SiteMap from './SiteMap';
import SearchBar from './SearchBar';
import NavButton from './NavButton';
import styled from 'styled-components';

const INITIAL_INPUT = {
  name: '',
  birthday: '',
  email: '',
  emailOk: '',
  password: '',
  passwordOk: '',
};

function Nav() {
  const [showModal, setShowModal] = useState(false);
  const [isSignUp, setIsSignUp] = useState(false);
  const [loginInput, setLoginInput] = useState(INITIAL_INPUT);

  const LOGIN = {
    type: 'login',
    text: '',
    data: [
      {
        type: 'email',
        name: 'email',
        value: loginInput.email,
        text: '',
        holder: '',
        data: '   .',
      },
      {
        type: 'password',
        name: 'password',
        value: loginInput.password,
        text: '',
        holder: '',
        data: ' ,,  8~12 .',
      },
    ],
  };

  const SIGNUP = {
    type: 'signUp',
    text: '',
    data: [
      {
        type: 'name',
        name: 'name',
        value: loginInput.name,
        text: '',
        holder: '',
        data: '  .',
      },
      {
        type: 'birthday',
        name: 'birthday',
        value: loginInput.birthday,
        text: ' 8 ex) 1990-05-05',
        holder: '',
        data: '  .',
      },
      {
        type: 'email',
        name: 'emailOk',
        value: loginInput.emailOk,
        text: '',
        holder: '',
        data: '  .',
      },
      {
        type: 'password',
        name: 'password',
        value: loginInput.password,
        text: '',
        holder: '',
        data: ' ,,  8~12 .',
      },
      {
        type: 'password',
        name: 'passwordOk',
        value: loginInput.passwordOk,
        text: ' ',
        holder: '    ',
        data: '    .',
      },
    ],
  };

  const setValues = e => {
    const { name, value } = e.target;
    setLoginInput({ ...loginInput, [name]: value });
  };

  const loginSetup = () => {
    setLoginInput(prev => ({
      ...prev,
      name: '',
      birthday: '',
      email: '',
      emailOk: '',
      password: '',
      passwordOk: '',
    }));

    setShowModal(prev => !prev);
  };

  const goToLogin = () => {
    setIsSignUp(false);
    loginSetup();
  };

  const goToSignUp = () => {
    setIsSignUp(true);
    loginSetup();
  };

  const handleModal = () => {
    setShowModal(prev => !prev);
  };

  const onClickSignUp = () => {
    setLoginInput(prev => ({
      ...prev,
      name: '',
      birthday: '',
      email: '',
      emailOk: '',
      password: '',
      passwordOk: '',
    }));
    setIsSignUp(prev => !prev);
  };

  const onClickWindow = e => {
    const clicked = e.target.closest('.modal');
    if (clicked) {
      return;
    }
    handleModal();
  };

  const onSign = format => {
    const signup = format.type;

    if (signup === 'signUp') {
      requestSignup(loginInput);
    } else {
      requestSignIn(loginInput);
    }
  };

  const requestSignup = loginInput => {
    const { name, birthday, emailOk, password } = loginInput;
    customFetch(
      API.SIGNUP,
      {
        body: JSON.stringify({
          name: name,
          birth_day: birthday,
          email: emailOk,
          password: password,
        }),
      },
      {
        onSuccess: res => {
          if (res.MESSAGE === 'SUCCESS') {
            alert(' ');
            onClickSignUp();
          } else {
            alert('    ');
          }
        },
      },
    );
  };

  const requestSignIn = loginInput => {
    const { email, password } = loginInput;
    customFetch(API.SIGNIN, {
      body: JSON.stringify(
        {
          email: email,
          password: password,
        },
        {
          onSuccess: res => {
            if (res.token) {
              localStorage.setItem('token', res.token);
            }
            if (res.MESSAGE === 'SUCCESS') {
              alert('@@@   ');
              handleModal();
            } else {
              alert('     ');
            }
          },
        },
      ),
    });
  };

  const [isSiteMapClicked, setIsSiteMapClicked] = useState(false);
  const [isSearchClicked, setIsSearchClicked] = useState(false);

  const onSiteMapClick = () => {
    if (isSearchClicked) setIsSearchClicked(!isSearchClicked);
    setIsSiteMapClicked(!isSiteMapClicked);
  };

  const onSearchClick = () => {
    if (isSiteMapClicked) setIsSiteMapClicked(!isSiteMapClicked);
    setIsSearchClicked(!isSearchClicked);
  };

  return (
    <>
      <Navigator>
        {showModal && (
          <Modal
            handleModal={handleModal}
            onClickWindow={onClickWindow}
            isSignUp={isSignUp}
            onClickSignUp={onClickSignUp}
            setValues={setValues}
            inputValue={loginInput}
            format={isSignUp ? SIGNUP : LOGIN}
            onSign={onSign}
          />
        )}
        <Container>
          <UserSupportWrapper>
            <div dis>
              <CustomerServiceLink to="">A1</CustomerServiceLink>
              <CustomerServiceLink to="">A2</CustomerServiceLink>
              <CustomerServiceLink to="">A3</CustomerServiceLink>
            </div>
            <div>
              <UserLink to="/" onClick={goToLogin}>
                
              </UserLink>
              <UserLink to="/" onClick={goToSignUp}>
                
              </UserLink>
            </div>
          </UserSupportWrapper>
          <CategoryWrapper>
            <CategorySubWrapper>
              {/*<NavButton
                onClick={onSiteMapClick}
                isClicked={isSiteMapClicked}
                type={'sitemap'}
              ></NavButton>*/}
              {/*<NavButton
                onClick={onSearchClick}
                isClicked={isSearchClicked}
                type={'search'}
              ></NavButton>*/}
              <Categories>
                {leftCategoryList.map((category, index) => (
                  <LeftCategory key={index}>
                    <LowerLeftLink
                      to={category.name === '' && '/moviepost'}
                    >
                      {category.name}
                    </LowerLeftLink>
                  </LeftCategory>
                ))}
              </Categories>
            </CategorySubWrapper>
            <Link to="">
              <Logo
                src={process.env.PUBLIC_URL + '/image/full_logo_white.png'}
                alt="full_logo_white"
              />
            </Link>
            <CategorySubWrapper>
              <Categories>
                {rightCategoryList.map((category, index) => (
                  <RightCategory key={index}>
                    <LowerRightLink to="">{category.name}</LowerRightLink>
                  </RightCategory>
                ))}
              </Categories>
              <Button>
                <i className="fas fa-table"></i>
              </Button>
              <Button>
                <i className="far fa-user"></i>
              </Button>
            </CategorySubWrapper>
          </CategoryWrapper>
        </Container>
        <SiteMap isDisplayed={isSiteMapClicked}></SiteMap>
        <SearchBar isDisplayed={isSearchClicked}></SearchBar>
      </Navigator>
    </>
  );
}
export default withRouter(Nav);

